* {
  margin: 0;
  padding: 0;
}

.navbar {
  background-color: #f2f2f2;
  position: fixed !important;
  width: 100%;
  z-index: 3 !important;
}

.logo {
  width: 280px;
  height: 110px;
  margin-left: 10px;
  margin-right: 20px;
  padding: 25px;
}

.navbar-nav ul li{
  list-style: none;
  padding: 10px;
}

.nav-item-si a{
  font-size: 0.9em;
  letter-spacing: 2px;
  font-weight: 600;
  padding-left: 10px;
}

.nav-item-si a:hover{
  background-color: rgb(238, 238, 238);
  color: rgb(252, 1, 1) !important;
}
  
.nav-item-su a{
  font-size: 0.9em;
  letter-spacing: 2px;
  font-weight: 600;
  margin-top: -10px;
}

.nav-item-su a:hover{
  background-color: rgb(247, 34, 34);
  color: white !important;
}

.dropdown-menu{
  width: 135px;
}

.navbar-nav{
  margin-left: auto;
}

.option{
  margin-right: 35px;
}

.user-authentic li{
  margin-left: -15px;
  margin-top: -10px;
  margin-bottom: -10px;
}

.dropdown-menu{
  min-width: 125px !important;
}

.dropdown-divider {
  margin-left: -10px !important;
  width: 95%;
}

.search-menu {
  position: absolute;
  display: flex;
  text-align: left;
  left: 400px;
  top: 85px;
  width: 70%;
}


.search{
  width: 50%;
  height: 35px;
  padding:15px;
  background:rgba(50, 50, 50, 0.2);
  border:0px solid #dbdbdb;
  border-radius: 5px;
}

.search-menu .btn {
  text-align: center;
  background-color: red;
  color:#fafafa;
  font-size: small;
  height: 35px;
  width: 75px;
  border-radius: 5px;
  border: none;
  margin-left: 10px;
}

.search-menu .btn:hover {
  opacity: 70%;
  color: white;
}

.logout a{
  color: red !important;
}

.logout a:hover{
  opacity: 70%;
}




@media (max-width: 1400px) {

  .navbar-nav{
    width: 375px;
    position: relative;
    right: 0px;
    margin-left: auto;
  }

  .navbar-nav li:nth-child(4){
    margin-right: 0px;
  }

  .navbar-nav li:nth-child(2),  .navbar-nav li:nth-child(3) {
    margin-right: 50px;
  }


  .search-menu {
    position: absolute;
    top: 40px;
    width: 124%;
    left: -120px;
  }

  .search{
    width: 100% !important;
  }


  .be-merch-comp{
    margin-top: 0px;
  }


  

}

@media (max-width: 990px) {

  .navbar-nav{
    margin-right: 0px;
      width: 98%;
      position: static;
      right: 0px;
  }

  .dropdown-divider {
    width: 99%;
  }

  .logo {
    width: 240px;
    height: 100px;
    margin-left: 0px;
  }

  .search-menu{
    position: static;
    margin-top: 0px;
    margin-bottom: 20px;
    width: 98%;
    margin-right: 35px;
 }
 
 
 .search{
   width: 100% !important;
   height: 35px;
   padding:15px;
   background:rgba(50, 50, 50, 0.2);
   border:0px solid #dbdbdb;
   border-radius: 5px;
 }
 
 .search-menu .btn {
   text-align: center;
   background-color: red;
   color:#fafafa;
   font-size: small;
   height: 35px;
   width: 75px;
   border-radius: 5px;
   border: none;
   margin-left: 10px;
 }

  .search-menu input{
    width: 100%;
  }

  .nav-item{
    margin-top: 0px;
}
  .option{
    width: 98%;
    padding-left: 10px;
  }


  .option:nth-child(2){
    border-bottom: 2px solid rgb(197, 197, 197);    
    border-top: 2px solid  rgb(197, 197, 197);
  }

  .option:nth-child(3){
    border-bottom: 2px solid rgb(197, 197, 197);    
  }

  .user-profile-authentic{
    border-top: 2px solid  rgb(197, 197, 197);
    margin-left: -10px;
    width: 101.4%;
  }

  .option:hover{
    background-color: rgb(238, 238, 238);
  }

  .navbar-form .input-group {
    margin: 0 auto;
  }

  .navbar-nav{
    text-align: left;
    margin-left: 30px;
  }

  .collapse ul li a{
    text-align: left;
  }

  .dropdown-menu{
    margin-left: 0px !important;
    width: 99%;
  }

  .nav-item-si a, .nav-item-su a{
    padding: 10px;
    padding-left: 10px !important;
  }

  .be-merch-comp{
    margin-left: 10px;
    color: gray;
  }
}

@media (max-width:850px) {
  .search-menu{
    width: 98% !important;
  }
}
