-webkit-appearance: button;
cursor: pointer;
}

.input-add-product h2{
    color: red;
    top: 90px;
    font-size: 1.5em;
    background-color: white;
    width: 100%; 
    font-weight: 600;
    grid-area: title;
}

/* name input */

.productNames{
    font-weight: 600;
    grid-area: productName;
    display: flex;
}

.name-input{
    width: 170px;
}

.productNames p{
    color: rgb(151, 151, 151);
}

.productNames input{
    border: none;
    border-bottom: 1px solid black;
    border-radius: 0%;
    margin-top: -10px;
    margin-left: 50px;
    width: 70%;
}

/* price input */

.productPrices{
    font-weight: 600;
    grid-area: productPrice;
    display: flex;
}

.price-input{
    width: 170px;
}

.productPrices p{
    color: rgb(151, 151, 151);
}

.productPrices #Curr{
    color: rgb(151, 151, 151);
    margin-left: 50px;
}

.productPrices input{
    border: none;
    border-bottom: 1px solid black;
    border-radius: 0%;
    margin-top: -10px;
    margin-left: 10px;
    width: 150px;
}

/* product desc */

.productDescriptions{
    font-weight: 600;
    grid-area: productDesc;
    width: 100%;
    display: flex;
}

.productDescriptions p{
    width: 200px;
    color: rgb(151, 151, 151);
}

.text-area-add-product{
    height: 450px;
    width: 70%;
    resize: none;
    padding: 10px;
    border: 2px solid rgb(219, 219, 219);
    overflow-y: auto;
    border-radius: 2px;
    margin-left: 50px;
}


/* product stock */

.productStocks{
    font-weight: 600;
    grid-area: productStock;
    width: 100%;
    display: flex;
}

.stock-input{
    width: 170px;
}

.productStocks input{
    border: none;
    border-bottom: 1px solid black;
    border-radius: 0%;
    margin-top: -10px;
    margin-left: 50px;
    width: 60px;
}

.productStocks p{
    color: rgb(151, 151, 151);
}

.productCategory{
    font-weight: 600;
    grid-area: productCategory;
    width: 100%;
    display: flex;
}

.category-input{
    width: 170px;
}

.productCategory input{
    border: none;
    border-bottom: 1px solid black;
    border-radius: 0%;
    margin-top: -10px;
    margin-left: 50px;
    width: 150px;
}

.productCategory p{
    color: rgb(151, 151, 151);
}

#equal-sign{
    margin-left: 30px;
}

.cancelButtonAP{
    border: 2px solid rgb(182, 182, 182);
    border-radius: 5px;
    width: 150px;
    background-color: white;
    color: red;
    font-weight: 600;
    font-size: 0.9em;
    height: 50px;
}

.SaveAndAddNew{
    border: 2px solid rgb(182, 182, 182);
    border-radius: 5px;
    width: 150px;
    background-color: white;
    color: red;
    font-weight: 600;
    font-size: 0.9em;
    margin-left: 25px;
    margin-right: 25px;
    height: 50px;
}


.Save{
    border: 2px solid rgb(230, 230, 230);
    border-radius: 5px;
    width: 150px;
    background-color: rgb(243, 11, 11);
    color: rgb(255, 255, 255);
    font-weight: 600;
    font-size: 0.9em;
    height: 50px;
}

.ButtonAddProducts{
    margin: auto;
    text-align: right;
    width: 90%;
    margin-top: 25px;
}




@media (max-width:1260px){
    #equal-sign{
        display: none;
    }

    .input-add-product{
        margin-top: 30px;
        display: grid;
        grid-template-areas:    "title"
                                "productName"
                                "productCategory"
                                "productPrice"
                                "productStock"
                                "productDesc";
        grid-template-columns: 100%;
        grid-template-rows: 0.7fr 1fr 1fr 1fr 1fr 4fr;
        width: 90%;
        height: 600px;
    }

    .productNames input, .productCategory input, .productStocks input, .productPrices input{
        border: 1.5px solid rgb(224, 224, 224);
        border-radius: 5px;
        height: 32px;
        margin-top: -5px;
        margin-left: 25px;
    }

    .productNames input {
        width: 68%;
    }
    
    .productPrices #Curr{
        margin-left: 25px;
    }

    .productDescriptions{
        display: flex;
    }

    .text-area-add-product{
        margin-top: 5px;
        height: 300px;
        width: 68%;
        margin-left: 0px;
    }

    .productCategory input{
        width: 190px;
    }

    .productNames, .productPrices, .productDescriptions, .productStocks, .productCategory{
        margin-left: 10px;

    }

}

@media (max-width:850px){

    .productImages{
        height: 240px;
        width: 90%;
    }

    .inputImage{
        width: 130px;
        height: 120px;
        margin-left: 15px;
    }

    .inputImageButton{
        width: 130px;
        height: 120px;
    }

    #primaryImage{
        font-size: 1em;
        margin-top: 10px;
    }
    
    .inputImage:nth-child(1){
        margin-left: 10px;
    }

    .productNames input{
        width: 61%;
    }

    .text-area-add-product{
        width: 61%;
    }
}

@media (max-width:700px){


    .inputImage:nth-child(1){
        margin-left: 0px;
    }

    .inputImage{
        margin-left: 10px;
    }

    .Add-Product-Content{
        margin-top: 120px;
        margin-left: 10px;
    }
    
    .Add-Product-Content h1{
        margin-top: 10px;
        margin-left: 25px;
        font-size: 1.8em;
    }

    .productImages h2, .input-add-product h2{
        font-size: 1.4em !important;
    }

    .productNames input{
        width: 50%;
    }

    .productCategory input{
        width: 30%;
    }

    .productPrices input{
        width: 30%;
        margin-left: 10px;
    }

    .productStocks input{
        width: 10%;
    }

    .text-area-add-product{
        width: 100% !important;
        padding: 5px;
        margin-left: 5px;
    }

    .productDescriptions{
        flex-direction: column;
    }
    
    .ButtonAddProducts{
        display: flex;
        justify-content: space-evenly;
    }

    .input-add-product{
        margin-top: -30px;
    }
}
