* {
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}

a {
  text-decoration: none;
}

.header {
  background-color: #f2f2f2;
  width: 100%;
  height: 10%;
}

.banklogo {
  height: 48px;
  width: 198px;
  float: left;
  padding: 20px;
  padding-left: 50px;
}

.listaccountbar li {
  display: inline;
  margin-right: 100px;
  margin-left: 75px;
}

.listaccountbar li {
  padding-top: 65px;
  float: left;
}

.cartbutton {
  float: right;
  height: 40px;
  width: 150px;
  margin-right: 100px;
  margin-top: 45px;
}

.My-Cart {
  background-color: red;
  border-top-left-radius: 10;
  border-bottom-left-radius: 10;
  border-right: white;
  border-style: solid;
  height: 40px;
  width: 120px;
  color: azure;
  font-weight: bold;
}

.cartQty {
  float: right;
  background-color: red;
  border-top-right-radius: 10;
  border-bottom-right-radius: 10;
  border-style: solid;
  height: 40px;
  width: 30px;
  color: azure;
  font-weight: bold;
  text-align: center;
}
.checkoutpage {
  margin-top: 1%;
  margin-right: 75%;
  font-size: 20;
  margin-bottom: 5px;
}

.productBar h4 {
  display: inline-block;
  font-size: 16;
  margin-right: 25px;
  margin-bottom: 15px;
}

h3 {
  margin-right: 75%;
  font-size: 16;
}

.chosenStatus {
  color: red;
  margin-left: 80%;
}

.tableP {
  width: 40%;
  height: 25%;
  border-width: 0px;
  border-collapse: collapse;
  margin-top: 2%;
  margin-left: 10%;
}

.row1A {
  background-color: rgb(240, 116, 116);
  border-bottom: 4px solid rgb(226, 226, 226);
  height: 15%;
}

.row1A td {
  font-size: 18;
  font-weight: bold;
  padding-left: 1%;
  color: rgb(0, 0, 0);
  letter-spacing: 2px;
}

.row2A {
  background-color: rgb(245, 245, 245);
  height: 35%;
  width: 10%;
}

.row2A td {
  font-size: 22;
  font-weight: bold;
  padding-left: 1%;
  color: rgb(0, 0, 0);
  letter-spacing: 2px;
  text-align: left;
}

.column1A {
  width: 18%;
}

.productImage {
  width: 65%;
  height: 15%;
  margin-left: 15%;
  margin-top: 5%;
  margin-bottom: 5%;
}

.row2A td {
  vertical-align: top;
  text-align: left;
}

.row2 .column2A,
.row2 .column3A {
  padding-top: 1%;
}

.row2 .column2A {
  width: 60%;
}

.inputNotes {
  width: 80%;
  height: 30%;
  margin-top: 3%;
  border-style: none;
  background-color: rgb(214, 214, 214);
}

textarea {
  max-width: 80%;
  max-height: 30%;
}

.footerProductDetailA {
  background-color: rgb(204, 204, 204);
  width: 80%;
  height: 3%;
  margin-left: 7%;
}

#freeshipping {
  font-style: italic;
  margin-left: -60%;
}

.removeA {
  font-weight: bold;
  color: rgb(255, 255, 255);
  float: right;
  margin-right: 1%;
  margin-top: 0.3%;
}

.totalPay {
  width: 70%;
  height: 3%;
}

.totalPay tr {
  margin-bottom: 1%;
}

.TotalBeforeShippingA p,
.shippingA p,
.totalA p {
  display: inline-block;
  font-size: 16;
  padding-bottom: 1%;
}

.CalculationA {
  text-align: left;
  margin-left: 20%;
}

.CalculationA .column1A {
  margin-left: 63%;
}

.TotalBeforeShippingA .column1A {
  margin-top: 2%;
}

.TotalBeforeShippingA .column2A {
  margin-left: 0.5%;
}

.shippingA .column2A {
  margin-left: 3.7%;
}

hr {
  width: 20%;
  border-style: solid;
  border-width: 2px;
  margin-left: 63%;
  margin-bottom: 1%;
  border-color: rgb(43, 43, 43);
}

.totalA .column1A,
.totalA .column2A {
  font-size: 20;
  font-weight: bolder;
}

.totalA .column2A {
  margin-left: -0.5%;
}

.PlaceOrderA {
  background-color: #ff0000;
  border-radius: 50;
  border-style: none;
  color: white;
  font-weight: bold;
  width: 8%;
  height: 60%;
  letter-spacing: 1px;
  margin-left: 76%;
  margin-top: 2%;
}

/*circle button*/
.btn-circle.btn-sm {
  width: 30px;
  height: 30px;
  padding: 6px 0px;
  border-radius: 15px;
  font-size: 8px;
  text-align: center;
}

.cf:before,
.cf:after {
  content: " "; /* 1 */
  display: table; /* 2 */
}

.cf:after {
  clear: both;
}

/**
  * For IE 6/7 only
  * Include this rule to trigger hasLayout and contain floats.
  */
.cf {
  *zoom: 1;
}
